body {
  margin: 0;
  padding: 2px 0 0 0;
  background-color: $bg;
  color: $fg;
  font-size: 1.1em;
  font-family: "Ubuntu", "Trebuchet MS", "Tahoma", "Helvetica", sans-serif;
  font-weight: 400;
  min-height: 100%;
  // background-image: url('/img/cam.png');
  // background-position: top;
  // background-size: cover;
  // background-repeat: no-repeat;
}

aside#topbar {
  position: fixed;
  width: 100%;
  height: 4px;
  left: 0;
  top: -2px;
  background-color: #d60270;
  background: linear-gradient(90deg,
      rgba(214, 2, 112, 1) 0%,
      rgba(155, 79, 150, 1) 50%,
      rgba(0, 56, 168, 1) 100%);
  // transition: width 1s ease-in;
  z-index: 999;

  &.bent {
    height: 8px;
    background: linear-gradient(to right, orange , yellow, green, cyan, blue, violet);
    // background-size: 400% 400%;
    // animation: bentbar 5s ease infinite;
  }
}

